<template>
  <div>
    <div class="bg">
      <div class="title">收到投递</div>
      <div class="list">
        <div>
          <Table :columns="columns1" :data="data">
            <template slot-scope="{ row, index }" slot="mmName">{{row.moneyManage.institutions}}</template>
            <template slot-scope="{ row, index }" slot="pmName">{{row.projectManage.name}}</template>
            <template slot-scope="{ row, index }" slot="caozuo">
              <Button
                v-if="row.isColler == '未收藏'"
                style="margin-right:10px;"
                @click="clickCollect(row.projectManage.code)"
              >收藏项目</Button>
              <Button
                v-else
                style="margin-right:10px;"
                @click="clickCollect(row.projectManage.code)"
              >收藏项目</Button>
              <Button style="margin-right:10px;" :to="`/project/detail/${row.projectManage.id}`">查看项目</Button>
              <Button :to="`/capital/detail/${row.moneyManage.id}`">查看资金</Button>
            </template>
          </Table>
        </div>
        <div class="cl"></div>
        <div class="page">
          <Page :total="seachConfig.pageTotal" @on-change="pageChange" show-elevator />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  getProjectDeliverys,
  projectDeliveryAdd,
  collect,
  cancelCollect
} from "../../../api/public";
import { messages } from "../../../utils";
import {
  Icon,
  Button,
  Page,
  Poptip,
  Tag,
  Table,
  Select,
  Option,
  Input
} from "iview";
export default {
  components: {
    [Icon.name]: Icon,
    [Page.name]: Page,
    [Poptip.name]: Poptip,
    [Table.name]: Table,
    [Tag.name]: Tag,
    [Select.name]: Select,
    [Option.name]: Option,
    [Input.name]: Input,
    [Button.name]: Button
  },
  data() {
    return {
      //搜索配置
      seachConfig: {
        type: "2",
        pageNo: 1, //当前页
        pageTotal: 1 //总条数
      },
      data: [],
      columns1: [
        {
          title: "项目名称",
          slot: "pmName"
        },
        {
          title: "资金名称",
          slot: "mmName"
        },
        {
          title: "投递时间",
          key: "createTime",
          width: 160
        },
        {
          title: "操作",
          slot: "caozuo",
          width: 300
        }
      ]
    };
  },
  methods: {
    getData() {
      getProjectDeliverys(this.seachConfig).then(res => {
        this.data = res.data.data.rows;
        this.seachConfig.pageTotal = res.data.data.total;
      });
    },
    //添加收藏
    clickCollect(code) {
      collect({ code, type: 1 }).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.success(`收藏成功`);
          this.getData();
        }
      });
    },
    //取消收藏
    clickCancelCollect(code) {
      cancelCollect({ code, type: 1 }).then(res => {
        if (res.data.status == "SUCCESS") {
          messages.success(`取消收藏成功`);
          this.getData();
        }
      });
    },
    //分页点击
    pageChange(page) {
      this.seachConfig.pageNo = page;
      this.getData();
    }
  },
  created() {
    this.getData();
  }
};
</script>
